@font-face {
  font-family: 'Jua';
  src: local('MyFont'), url(../assets/fonts/Jua-Regular.ttf) format('truetype');
}

html, body {
  /* background-color: #d3d3d3; */
  background-color: #fafafa;
  min-height: 100%;
