/*
===== Layout ======
*/

/*
 ======== Sidebar ======
*/

/* https://github.com/azouaoui-med/react-pro-sidebar/issues/10 */

.pro-sidebar {
  font-family: Jua;
  position: absolute;
  width: auto;
}

/* .pro-menu {} */

.sidebar {
  height: 100vh;
  grid-area: sidenav;
  width: fit-content;
}

.title-header {
  padding: 10px 0;
}

.pro-sidebar-header {
  text-align: center !important;
  font-size: 30px;
}

.pro-sidebar-footer {
  border-top: 0 !important;
}

/*
 ===== Dashboard ======
*/

.dashboard {
  width: auto;
  /* display: flex; */
  /* background-color: pink; */
  margin: 20px;
  flex: 1;
}

.grid {
  height: 100%
}

.dashboard-component {
  /* flexbox */
  /* display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center; */

  /* Sizing */
  width: fit-content;
  min-width: 150;
  /* Style */
  border: 5 solid #1d1d1d !important;
  border-radius: 5px;
  border-width: 10px;
  -webkit-box-shadow: 5px 5px 24px 2px rgba(0, 0, 0, 0.42);
  box-shadow: 5px 5px 24px 2px rgba(0, 0, 0, 0.42);
  font-family: Jua;
  background-color: #fafafa;
}

.react-grid-item.react-grid-placeholder {
  border-radius: 5px;
  border-width: 10px;
  background: #1d1d1d !important;
  opacity: 0.01 !important;
  height: max-content;
}


/*
======= Dashboard Components =======
*/

.recorder {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  /* width: 400px !important; */
  padding: 20px;
}

.recorder-controls {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 175px;
}

.timeDomGraph {
  flex: 1;
  height: 100%;
  width: 100%;
}